// src/app/billing/page.tsx - Updated with Hero UI
'use client';

import { useState, useEffect, useCallback } from 'react';
import {
    Plus,
    Download,
    Eye,
    Edit,
    Trash2,
    Mail,
    DollarSign,
    Search,
    FileText,
    CreditCard,
    Calendar,
    Users,
    Filter,
} from 'lucide-react';
import { Invoice, Payment } from '@/lib/types';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Table } from '@/components/ui/Table';
import { Modal } from '@/components/ui/Modal';
import { Input } from '@heroui/input';
import { Select, SelectItem } from '@heroui/select';
import { Chip } from '@heroui/chip';
import { Dropdown, DropdownTrigger, DropdownMenu, DropdownItem } from '@heroui/dropdown';
import { formatCurrency, formatDate } from '@/lib/utils';
import { api } from '@/lib/api';

export default function BillingPage() {
    // State
    const [invoices, setInvoices] = useState<Invoice[]>([]);
    const [payments, setPayments] = useState<Payment[]>([]);
    const [selectedKeys, setSelectedKeys] = useState<Set<string>>(new Set());

    // Modals
    const [isInvoiceModalOpen, setIsInvoiceModalOpen] = useState(false);
    const [isPaymentModalOpen, setIsPaymentModalOpen] = useState(false);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);

    // Selected invoice
    const [selectedInvoice, setSelectedInvoice] = useState<Invoice | null>(null);

    // Filters
    const [searchTerm, setSearchTerm] = useState('');
    const [statusFilter, setStatusFilter] = useState<string>('all');
    const [dateRange, setDateRange] = useState({ start: '', end: '' });

    // Fetch data
    const fetchData = useCallback(async () => {
        try {
            const [invoicesData, paymentsData] = await Promise.all([
                api.getInvoices(),
                api.getPayments(),
            ]);

            setInvoices(invoicesData || []);
            setPayments(paymentsData || []);
        } catch (error) {
            console.error('Error fetching billing data:', error);
        }
    }, []);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    // Filter invoices
    const filteredInvoices = invoices.filter(invoice => {
        const matchesSearch = searchTerm === '' ||
            invoice.clientName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            invoice.invoiceNumber.toLowerCase().includes(searchTerm.toLowerCase());

        const matchesStatus = statusFilter === 'all' || invoice.status === statusFilter;

        const matchesDate = (!dateRange.start || invoice.date >= dateRange.start) &&
            (!dateRange.end || invoice.date <= dateRange.end);

        return matchesSearch && matchesStatus && matchesDate;
    });

    // Table columns
    const columns = [
        {
            key: 'invoiceNumber',
            label: 'Invoice #',
            render: (invoice: Invoice) => (
                <div>
                    <div className="font-medium text-foreground">{invoice.invoiceNumber}</div>
                    <div className="text-sm text-default-500">{invoice.clientName}</div>
                </div>
            ),
        },
        {
            key: 'date',
            label: 'Date',
            render: (invoice: Invoice) => (
                <div>
                    <div>{formatDate(invoice.date)}</div>
                    <div className="text-sm text-default-500">Due: {formatDate(invoice.dueDate)}</div>
                </div>
            ),
        },
        {
            key: 'amount',
            label: 'Amount',
            render: (invoice: Invoice) => (
                <div className="font-bold text-foreground">{formatCurrency(invoice.total)}</div>
            ),
        },
        {
            key: 'status',
            label: 'Status',
            render: (invoice: Invoice) => {
                const statusColors: Record<string, any> = {
                    paid: { color: 'success', variant: 'flat' },
                    sent: { color: 'primary', variant: 'flat' },
                    overdue: { color: 'danger', variant: 'flat' },
                    draft: { color: 'default', variant: 'flat' },
                };

                const status = statusColors[invoice.status] || { color: 'default', variant: 'flat' };

                return (
                    <Chip
                        color={status.color}
                        variant={status.variant}
                        size="sm"
                    >
                        {invoice.status.charAt(0).toUpperCase() + invoice.status.slice(1)}
                    </Chip>
                );
            },
        },
        {
            key: 'actions',
            label: 'Actions',
            render: (invoice: Invoice) => (
                <div className="flex gap-2">
                    <Button
                        isIconOnly
                        size="sm"
                        variant="light"
                        onPress={() => handleViewInvoice(invoice)}
                    >
                        <Eye className="h-4 w-4" />
                    </Button>

                    <Dropdown>
                        <DropdownTrigger>
                            <Button
                                isIconOnly
                                size="sm"
                                variant="light"
                            >
                                <Edit className="h-4 w-4" />
                            </Button>
                        </DropdownTrigger>
                        <DropdownMenu aria-label="Invoice actions">
                            <DropdownItem key="view" onPress={() => handleViewInvoice(invoice)}>
                                View Details
                            </DropdownItem>
                            <DropdownItem key="edit" onPress={() => handleEditInvoice(invoice)}>
                                Edit Invoice
                            </DropdownItem>
                            <DropdownItem key="send" onPress={() => handleSendInvoice(invoice)}>
                                Send to Client
                            </DropdownItem>
                            <DropdownItem key="duplicate" onPress={() => handleDuplicateInvoice(invoice)}>
                                Duplicate Invoice
                            </DropdownItem>
                            <DropdownItem
                                key="delete"
                                color="danger"
                                onPress={() => handleDeleteInvoice(invoice)}
                            >
                                Delete Invoice
                            </DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </div>
            ),
        },
    ];

    // Handlers
    const handleViewInvoice = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        // Open view modal
    };

    const handleEditInvoice = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        setIsInvoiceModalOpen(true);
    };

    const handleDeleteInvoice = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        setIsDeleteModalOpen(true);
    };

    const handleSendInvoice = async (invoice: Invoice) => {
        try {
            await api.sendInvoice(invoice.id);
            await fetchData();
        } catch (error) {
            console.error('Error sending invoice:', error);
        }
    };

    const handleDuplicateInvoice = async (invoice: Invoice) => {
        // Implementation for duplicating invoice
        console.log('Duplicate invoice:', invoice);
    };

    const handleCreateInvoice = () => {
        setSelectedInvoice(null);
        setIsInvoiceModalOpen(true);
    };

    const handleExport = () => {
        const csv = [
            ['Invoice #', 'Client', 'Date', 'Due Date', 'Status', 'Amount'].join(','),
            ...filteredInvoices.map(inv => [
                inv.invoiceNumber,
                inv.clientName,
                inv.date,
                inv.dueDate,
                inv.status,
                inv.total,
            ].join(',')),
        ].join('\n');

        const blob = new Blob([csv], { type: 'text/csv' });
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `invoices-${new Date().toISOString().split('T')[0]}.csv`;
        a.click();
    };

    const handleRecordPayment = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        setIsPaymentModalOpen(true);
    };

    const stats = {
        totalRevenue: payments
            .filter(p => p.status === 'completed')
            .reduce((sum, p) => sum + p.amount, 0),
        pendingInvoices: invoices.filter(i => i.status === 'sent').length,
        paidInvoices: invoices.filter(i => i.status === 'paid').length,
        overdueInvoices: invoices.filter(i => i.status === 'overdue').length,
    };

    return (
        <div className="space-y-6 animate-fade-in">
            {/* Header */}
            <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-foreground">Billing</h1>
                    <p className="text-default-500 mt-2">Manage invoices, payments, and billing cycles</p>
                </div>
                <div className="flex gap-3">
                    <Button
                        variant="bordered"
                        startContent={<Download className="h-4 w-4" />}
                        onPress={handleExport}
                    >
                        Export
                    </Button>
                    <Button
                        color="primary"
                        startContent={<Plus className="h-4 w-4" />}
                        onPress={handleCreateInvoice}
                    >
                        New Invoice
                    </Button>
                </div>
            </div>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-success/10 rounded-xl">
                            <DollarSign className="h-6 w-6 text-success" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Total Revenue</p>
                            <p className="text-2xl font-bold mt-1">{formatCurrency(stats.totalRevenue)}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-primary/10 rounded-xl">
                            <FileText className="h-6 w-6 text-primary" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Paid Invoices</p>
                            <p className="text-2xl font-bold mt-1">{stats.paidInvoices}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-warning/10 rounded-xl">
                            <CreditCard className="h-6 w-6 text-warning" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Pending</p>
                            <p className="text-2xl font-bold mt-1">{stats.pendingInvoices}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-danger/10 rounded-xl">
                            <Calendar className="h-6 w-6 text-danger" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Overdue</p>
                            <p className="text-2xl font-bold mt-1">{stats.overdueInvoices}</p>
                        </div>
                    </div>
                </Card>
            </div>

            {/* Filters */}
            <Card>
                <div className="p-4">
                    <div className="flex flex-col md:flex-row gap-4">
                        <div className="flex-1">
                            <Input
                                placeholder="Search invoices by client or invoice #"
                                startContent={<Search className="h-4 w-4 text-default-400" />}
                                value={searchTerm}
                                onValueChange={setSearchTerm}
                                className="max-w-lg"
                            />
                        </div>

                        <div className="flex flex-wrap gap-3">
                            <Select
                                className="w-40"
                                size="sm"
                                selectedKeys={[statusFilter]}
                                onSelectionChange={(keys) => setStatusFilter(Array.from(keys)[0] as string)}
                            >
                                <SelectItem key="all">All Status</SelectItem>
                                <SelectItem key="draft">Draft</SelectItem>
                                <SelectItem key="sent">Sent</SelectItem>
                                <SelectItem key="paid">Paid</SelectItem>
                                <SelectItem key="overdue">Overdue</SelectItem>
                            </Select>

                            <Input
                                type="date"
                                size="sm"
                                className="w-40"
                                placeholder="From"
                                value={dateRange.start}
                                onValueChange={(value) => setDateRange(prev => ({ ...prev, start: value }))}
                            />

                            <Input
                                type="date"
                                size="sm"
                                className="w-40"
                                placeholder="To"
                                value={dateRange.end}
                                onValueChange={(value) => setDateRange(prev => ({ ...prev, end: value }))}
                            />

                            <Button
                                variant="light"
                                startContent={<Filter className="h-4 w-4" />}
                                onPress={() => {
                                    setSearchTerm('');
                                    setStatusFilter('all');
                                    setDateRange({ start: '', end: '' });
                                }}
                            >
                                Clear Filters
                            </Button>
                        </div>
                    </div>
                </div>
            </Card>

            {/* Invoices Table */}
            <Card>
                <Table
                    data={filteredInvoices}
                    columns={columns}
                    selectionMode="multiple"
                    selectedKeys={selectedKeys}
                    onSelectionChange={setSelectedKeys as any}
                    emptyMessage="No invoices found. Create your first invoice!"
                />

                {/* Table Footer */}
                <div className="flex items-center justify-between border-t border-divider p-4">
                    <div className="text-sm text-default-500">
                        Showing {filteredInvoices.length} of {invoices.length} invoices
                    </div>
                    <div className="flex gap-2">
                        <Button
                            variant="light"
                            size="sm"
                            isDisabled
                        >
                            Previous
                        </Button>
                        <Button
                            color="primary"
                            size="sm"
                        >
                            1
                        </Button>
                        <Button
                            variant="light"
                            size="sm"
                        >
                            Next
                        </Button>
                    </div>
                </div>
            </Card>

            {/* Recent Payments */}
            <Card>
                <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-semibold text-foreground">Recent Payments</h2>
                    <Button
                        variant="light"
                        size="sm"
                    >
                        View All
                    </Button>
                </div>

                <div className="space-y-4">
                    {payments.slice(0, 5).map(payment => (
                        <div key={payment.id} className="flex items-center justify-between p-3 hover:bg-default-50 rounded-xl transition-colors">
                            <div className="flex items-center gap-4">
                                <div className="p-2 bg-success/10 rounded-lg">
                                    <DollarSign className="h-5 w-5 text-success" />
                                </div>
                                <div>
                                    <p className="font-medium text-foreground">{payment.clientName}</p>
                                    <p className="text-sm text-default-500">
                                        {payment.invoiceNumber} â€¢ {payment.method.replace('_', ' ').toUpperCase()}
                                    </p>
                                </div>
                            </div>
                            <div className="text-right">
                                <p className="font-bold text-foreground">{formatCurrency(payment.amount)}</p>
                                <p className="text-sm text-default-500">{formatDate(payment.date)}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </Card>

            {/* Invoice Modal */}
            <Modal
                isOpen={isInvoiceModalOpen}
                onOpenChange={setIsInvoiceModalOpen}
                title={selectedInvoice ? 'Edit Invoice' : 'Create New Invoice'}
                size="4xl"
            >
                <div className="space-y-4">
                    <p>Invoice form will go here...</p>
                </div>
            </Modal>

            {/* Payment Modal */}
            <Modal
                isOpen={isPaymentModalOpen}
                onOpenChange={setIsPaymentModalOpen}
                title="Record Payment"
            >
                <div className="space-y-4">
                    <p>Payment form will go here...</p>
                </div>
            </Modal>

            {/* Delete Confirmation Modal */}
            <Modal
                isOpen={isDeleteModalOpen}
                onOpenChange={setIsDeleteModalOpen}
                title="Delete Invoice"
            >
                <p>
                    Are you sure you want to delete invoice #{selectedInvoice?.invoiceNumber}?
                    This action cannot be undone.
                </p>
                <div className="flex justify-end gap-2 mt-6">
                    <Button
                        variant="light"
                        onPress={() => setIsDeleteModalOpen(false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        color="danger"
                        onPress={() => {
                            // Handle delete
                            setIsDeleteModalOpen(false);
                        }}
                    >
                        Delete Invoice
                    </Button>
                </div>
            </Modal>
        </div>
    );
}